@font-face {
  font-family: 'mm';
  src: url("../fonts/Montserrat-Medium.ttf");
}

header .navbar {
  background: #FFFFFF;
  box-shadow: 0 2px 5px 0 rgba(17, 130, 207, 0.16), 0 2px 10px 0 rgba(17, 130, 207, 0.12) !important;
}

header .navbar .navbar-toggler .animated-icon1 {
  cursor: pointer;
  height: 20px;
  margin: 0px;
  position: relative;
  transform: rotate(0deg);
  transition: 0.5s ease-in-out;
  width: 30px;
}

header .navbar .navbar-toggler .animated-icon1 span {
  background: #0096DB;
  border-radius: 9px;
  display: block;
  height: 3px;
  left: 0;
  opacity: 1;
  position: absolute;
  transform: rotate(0deg);
  transition: 0.25s ease-in-out;
  width: 100%;
}

header .navbar .navbar-toggler .animated-icon1 span:nth-child(1) {
  top: 0px;
}

header .navbar .navbar-toggler .animated-icon1 span:nth-child(2) {
  top: 10px;
}

header .navbar .navbar-toggler .animated-icon1 span:nth-child(3) {
  top: 20px;
}

header .navbar .navbar-toggler .animated-icon1.open span:nth-child(1) {
  -moz-transform: rotate(135deg);
  -o-transform: rotate(135deg);
  -webkit-transform: rotate(135deg);
  top: 11px;
  transform: rotate(135deg);
}

header .navbar .navbar-toggler .animated-icon1.open span:nth-child(2) {
  left: -60px;
  opacity: 0;
}

header .navbar .navbar-toggler .animated-icon1.open span:nth-child(3) {
  -moz-transform: rotate(-135deg);
  -o-transform: rotate(-135deg);
  -webkit-transform: rotate(-135deg);
  top: 11px;
  transform: rotate(-135deg);
}

header .navbar .collapse .navbar-nav .nav-item .nav-link,
header .navbar .collapsing .navbar-nav .nav-item .nav-link {
  font-family: 'mm';
  font-size: 14px;
}

header .navbar .collapse .navbar-nav .nav-item .after,
header .navbar .collapsing .navbar-nav .nav-item .after {
  position: relative;
}

header .navbar .collapse .navbar-nav .nav-item .after li,
header .navbar .collapsing .navbar-nav .nav-item .after li {
  padding-right: 10px;
  list-style: none;
}

header .navbar .collapse .navbar-nav .nav-item .after li .nav-link,
header .navbar .collapsing .navbar-nav .nav-item .after li .nav-link {
  border-radius: 10px;
}

header .navbar .collapse .navbar-nav .nav-item .after .dropdown-toggle[aria-expanded="true"] .down-caret,
header .navbar .collapsing .navbar-nav .nav-item .after .dropdown-toggle[aria-expanded="true"] .down-caret {
  transform: rotate(180deg);
  color: #0096DB;
}

header .navbar .collapse .navbar-nav .nav-item .after .dropdown-toggle .down-caret,
header .navbar .collapsing .navbar-nav .nav-item .after .dropdown-toggle .down-caret {
  transition: all 0.4s ease;
}

header .navbar .collapse .navbar-nav .nav-item .after .dropdown .nav-link::after,
header .navbar .collapsing .navbar-nav .nav-item .after .dropdown .nav-link::after {
  display: none;
}

header .navbar .collapse .navbar-nav .nav-item .after .dropdown .nav-link i,
header .navbar .collapsing .navbar-nav .nav-item .after .dropdown .nav-link i {
  color: #CDDDEC;
}

header .navbar .collapse .navbar-nav .nav-item .after .dropdown-menu,
header .navbar .collapsing .navbar-nav .nav-item .after .dropdown-menu {
  border: none;
  border-radius: unset;
  padding: 10px;
  box-shadow: 0px 10px 20px 10px rgba(50, 130, 207, 0.1);
  margin-left: -40px;
  margin-top: 10px;
}

header .navbar .collapse .navbar-nav .nav-item .after .dropdown-menu .dropdown-item,
header .navbar .collapsing .navbar-nav .nav-item .after .dropdown-menu .dropdown-item {
  font-family: 'mm';
  font-size: 14px;
}

header .navbar .collapse .navbar-nav .nav-item .after .dropdown-menu .dropdown-item:hover,
header .navbar .collapsing .navbar-nav .nav-item .after .dropdown-menu .dropdown-item:hover {
  color: #0096DB;
}

header .navbar .collapse .navbar-nav .nav-item .after .dropdown.show .nav-link,
header .navbar .collapsing .navbar-nav .nav-item .after .dropdown.show .nav-link {
  color: #0096DB;
}

header .navbar .collapse .navbar-nav .nav-item .after::after,
header .navbar .collapsing .navbar-nav .nav-item .after::after {
  content: "";
  width: 1px;
  height: 100%;
  position: absolute;
  background: #CDDDEC;
  right: -15px;
}

header .navbar .collapse .navbar-nav .nav-item .register li,
header .navbar .collapsing .navbar-nav .nav-item .register li {
  padding-left: 10px;
  list-style: none;
}

header .navbar .collapse .navbar-nav .nav-item .register li .nav-link,
header .navbar .collapsing .navbar-nav .nav-item .register li .nav-link {
  border-radius: 10px;
}

header .navbar .collapse .navbar-nav .nav-item .register li img,
header .navbar .collapsing .navbar-nav .nav-item .register li img {
  margin-bottom: 3px;
}

header .navbar .collapse .navbar-nav .nav-item .register .dropdown-toggle[aria-expanded="true"] .down-caret,
header .navbar .collapsing .navbar-nav .nav-item .register .dropdown-toggle[aria-expanded="true"] .down-caret {
  transform: rotate(180deg);
  color: #0096DB;
}

header .navbar .collapse .navbar-nav .nav-item .register .dropdown-toggle .down-caret,
header .navbar .collapsing .navbar-nav .nav-item .register .dropdown-toggle .down-caret {
  transition: all 0.4s ease;
}

header .navbar .collapse .navbar-nav .nav-item .register .dropdown .nav-link::after,
header .navbar .collapsing .navbar-nav .nav-item .register .dropdown .nav-link::after {
  display: none;
}

header .navbar .collapse .navbar-nav .nav-item .register .dropdown .nav-link i,
header .navbar .collapsing .navbar-nav .nav-item .register .dropdown .nav-link i {
  color: #CDDDEC;
}

header .navbar .collapse .navbar-nav .nav-item .register .dropdown-menu,
header .navbar .collapsing .navbar-nav .nav-item .register .dropdown-menu {
  border: none;
  border-radius: unset;
  padding: 10px;
  box-shadow: 0px 10px 20px 10px rgba(50, 130, 207, 0.1);
  margin-left: -40px;
  margin-top: 10px;
}

header .navbar .collapse .navbar-nav .nav-item .register .dropdown-menu .dropdown-item,
header .navbar .collapsing .navbar-nav .nav-item .register .dropdown-menu .dropdown-item {
  font-family: 'mm';
  font-size: 14px;
}

header .navbar .collapse .navbar-nav .nav-item .register .dropdown-menu .dropdown-item:hover,
header .navbar .collapsing .navbar-nav .nav-item .register .dropdown-menu .dropdown-item:hover {
  color: #0096DB;
}

header .navbar .collapse .navbar-nav .nav-item .register .dropdown-toggle,
header .navbar .collapsing .navbar-nav .nav-item .register .dropdown-toggle {
  border: 1px solid #CDDDEC;
  border-radius: 5px !important;
  padding: 2px 37px 2px 11px;
  margin-top: 5px;
}

header .navbar .collapse .navbar-nav .nav-item .register .dropdown-toggle i,
header .navbar .collapsing .navbar-nav .nav-item .register .dropdown-toggle i {
  position: absolute;
  top: 7px;
  right: 7px;
}

header .navbar .collapse .navbar-nav .nav-item .register .dropdown-toggle::before,
header .navbar .collapsing .navbar-nav .nav-item .register .dropdown-toggle::before {
  content: "";
  width: 29px;
  position: absolute;
  bottom: 0px;
  left: 49px;
  height: 100%;
  background: #EDF5FC;
  border-left: 1px solid #CDDDEC;
}

header .navbar .collapse .navbar-nav .nav-item .register .dropdown-menu,
header .navbar .collapsing .navbar-nav .nav-item .register .dropdown-menu {
  min-width: 50px !important;
  margin-left: 0;
}

header .navbar .collapse .navbar-nav .nav-item .register .dropdown.show .nav-link,
header .navbar .collapsing .navbar-nav .nav-item .register .dropdown.show .nav-link {
  color: #0096DB;
}

@keyframes slideIn {
  0% {
    transform: translateY(1rem);
    opacity: 0;
  }

  100% {
    transform: translateY(0rem);
    opacity: 1;
  }

  0% {
    transform: translateY(1rem);
    opacity: 0;
  }
}

header .slideIn {
  animation-name: slideIn;
}

@media (min-width: 992px) {
  header .animate {
    animation-duration: 0.3s;
    animation-fill-mode: both;
  }
}

@media (max-width: 992px) {
  header .navbar .collapse .navbar-nav .nav-item .after,
  header .navbar .collapsing .navbar-nav .nav-item .after {
    flex-flow: column;
    padding-left: 0;
  }

  header .navbar .collapse .navbar-nav .nav-item .after li,
  header .navbar .collapsing .navbar-nav .nav-item .after li {
    padding-right: 0;
  }

  header .navbar .collapse .navbar-nav .nav-item .after li .dropdown-menu,
  header .navbar .collapsing .navbar-nav .nav-item .after li .dropdown-menu {
    margin-left: 0;
    width: 220px;
  }

  header .navbar .collapse .navbar-nav .nav-item .after::after,
  header .navbar .collapsing .navbar-nav .nav-item .after::after {
    display: none;
  }

  header .navbar .collapse .navbar-nav .nav-item .register,
  header .navbar .collapsing .navbar-nav .nav-item .register {
    flex-flow: column;
    padding-left: 0;
  }

  header .navbar .collapse .navbar-nav .nav-item .register li,
  header .navbar .collapsing .navbar-nav .nav-item .register li {
    padding-right: 0;
  }

  header .navbar .collapse .navbar-nav .nav-item .register li .dropdown-menu,
  header .navbar .collapsing .navbar-nav .nav-item .register li .dropdown-menu {
    margin-left: 0;
    width: 220px;
  }

  header .navbar .collapse .navbar-nav .nav-item .register::after,
  header .navbar .collapsing .navbar-nav .nav-item .register::after {
    display: none;
  }

  header .navbar .collapse .navbar-nav .nav-item .register li,
  header .navbar .collapsing .navbar-nav .nav-item .register li {
    padding-left: 0px;
  }

  header .navbar .collapse .navbar-nav .nav-item .register li .dropdown-menu,
  header .navbar .collapsing .navbar-nav .nav-item .register li .dropdown-menu {
    width: 80px !important;
    padding: 0 !important;
  }

  header .navbar .collapse .navbar-nav .nav-item .register li .dropdown-toggle,
  header .navbar .collapsing .navbar-nav .nav-item .register li .dropdown-toggle {
    display: inline-block;
  }

  header .navbar .collapse.show {
    overflow-x: unset !important;
    overflow-y: unset !important;
  }
}